*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  list-style-type: none;
  border: none;
  text-decoration: none;
  border: 0;
}
:root {
  /* --primary-hue: 20;
    --gary-hue:280; */
  --primary-hue: 210;
  --gray-hue: 240;
  --color-primary: hsl(var(--primary-hue), 100%, 50%);
  --color-primary-variant: hsl(var(--primary-hue), 100%, 65%);
  --color-secondary: #ffb116;
  --color-gray-100: hsl(var(--gray-hue), 47%, 94%);
  --color-gray-200: hsl(var(--gray-hue), 26%, 75%);
  --color-gray-300: hsl(var(--gray-hue), 24%, 64%);
  --color-gray-400: hsl(var(--gray-hue), 16%, 41%);
  --color-gray-500: hsl(var(--gray-hue), 44%, 25%);
  --color-gray-600: hsl(var(--gray-hue), 48%, 15%);

  --containr-width-lg: 80%;
  --container-width-md: 90%;

  --transition: all 0.3s ease-in;
}

/* General Styles */
body{
  font-family: 'Montserrat', sans-serif;
  color: var(--color-gray-200);
  line-height: 1.7;
  overflow-x: hidden;
  background: var(--color-gray-600);
}
.container{
  width: var(--containr-width-lg);
  max-width: 1920px;
  margin: 0 auto;

}
h1, h2, h3, h4, h5{
  line-height: 1.2;
  color: var(--color-gray-100);
}
h1{
  font-size: 3rem;

}
h2{
  font-size: 2rem;

}
a {
  color: var(--color-gray-100);
}
img{
  width:100% ;
  display: block;
  object-fit: cover;
}
.btn{
  color: var(--color-gray-100);
  width: fit-content;
  margin: 0 auto;
  padding: .9rem 2rem;
  background-color: var(--color-primary);
  border-radius: 1.5rem;
  transition: var(--transition);
}
.btn:hover{
  background-color: var(--color-secondary);
  color: var(--color-gray-600);

}
.btn.lg{
  padding: 1.2rem 3rem;
  border-radius: 2rem;
  font-size: 1.1rem;
}
.btn.sm{
  padding: .4rem 1.2rem;
  font-size: .9rem;

}
.btn.sm:hover{
  background-color: var(--color-gray-100);
}

section{
  margin-top: 10rem;

}

.section__head{
  display: flex;
  align-items: center;
  gap: 1rem;

}
.section__head span{
  background-color: var(--color-gray-500);
  padding: .7rem;
  border-radius: 1rem;
  color: var(--color-secondary);
  font-size: 1.5rem;
}

.card{
  background-color: var(--color-gray-500);
  text-align: center;
  border: 2px solid transparent;
  padding: 3rem 2rem;
  border-radius: 0 3rem 0 3rem;
  transition: var(--transition);
}
.card:hover{
  background: transparent;
  border-color: var(--color-gray-400);
  cursor: default;
}
.card span{
  width: 3rem;
  height: 3rem;
  background: var(--color-primary);
  color: var(--color-gray-100);
  font-size: 1.5rem;
  padding: .8rem;
  display: grid;
  place-items: center;
  margin-bottom: 1.5rem;
  margin-inline: auto;
  border-radius: 1rem;
  transition: var(--transition);
}
.card:hover span{
  background: var(--color-secondary);
  color: var(--color-gray-600);
}

.card small{
  margin-top: 1rem;
  display: block;
  font-weight: 300;
  
}

/* Header  */
.header{
  height: 20rem;
  overflow: hidden;
  margin-top: 5rem;
  border-bottom: 2px solid var(--color-gray-200);
}
.header__container{
  width: 100%;
  height: 100%;
  position: relative;
  display: grid;
  place-items: center;
  background: black;
}
.header__container-bg{
  position: absolute;
  width: 100%;
}

.header__container-bg img{
  opacity: .5;
}
.header__content{
  position: relative;
  width: 45%;
  margin: 0 auto;
  text-align: center;
  color: var(--color-gray-100);
}
.header__content h2{
  margin-bottom: 1rem;
}
.header__content p{
  color: var(--color-gray-200);
}


/* ============ media queries ========== */
@media screen and (max-width: 1024px){
  .container {
    width: var(--container-width-md);
  }
  .header__content{
    width: 75%;
  }
}
/* ============ media queries ========== */
@media screen and (max-width: 600px){
  section{
    margin-top: 7rem;
  }
  .header{
    height: fit-content;
  }

  .header__content{
    width: var(--container-width-md);
    padding: 3rem 0;
  }
    .header__content p{
      font-size: .8rem;
    }
}




